// MyAxios.js
import axios from "axios";
import qs from "qs";
import store from "@/store";
// axios
const instance = axios.create();

//axios,,token
instance.interceptors.request.use(function (config) {
  config.headers["Authorization"] = store.state.token;
  return config;
});

let myaxios = {
  /**
   * get
   * @param {string} url  
   * @param {object} params  
   * @returns Promise
   */
  get(url, params) {
    return instance({
      url,
      method: "GET",
      params,
    });
  },

  /**
   * post
   * @param {string} url  
   * @param {object} params  
   * @returns Promise
   */
  post(url, params) {
    return instance({
      url,
      method: "POST",
      data: qs.stringify(params),
    });
  },
};

export default myaxios;
